import React, { useContext } from 'react'
import { FixedSizeList as List } from 'react-window'
import styled from 'styled-components'
import { observer } from 'mobx-react-lite'
import { UncontrolledTooltip } from 'reactstrap'
import { FaTrashAlt } from 'react-icons/fa'
import { FaRegEdit } from 'react-icons/fa'

import storeContext from '../../storeContext'

const Container = styled.div`
  border-right: 1px solid rgb(46, 125, 50);
`
const Row = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  border-bottom: 1px solid rgba(46, 125, 50, 0.5);
  cursor: pointer;
  background-color: ${props => (props.active ? 'rgb(255, 250, 198)' : 'unset')};
  border-top: ${props =>
    props.active ? '1px solid rgba(46, 125, 50, 0.5)' : 'unset'};
  height: 50px;
  padding: 15px;
  line-height: 1.25em;
  &:hover {
    background-color: rgb(255, 250, 198);
    border-top: 1px solid rgba(46, 125, 50, 0.5);
    margin-top: -1px;
  }
`
const RowContainer = styled.div`
  display: flex;
`
const Text = styled.div`
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  flex-shrink: 1;
  flex-grow: 1;
`
const Infos = styled.div`
  display: flex;
  flex-shrink: 0;
  flex-grow: 1;
  justify-content: flex-end;
`
const Svg = styled.div`
  margin-left: 4px;
  font-size: large;
  display: flex;
`
const MutationFrist = styled.div`
  padding-right: 5px;
  font-size: 1rem;
`

const AbteilungList = ({ dimensions, activeId }) => {
  const store = useContext(storeContext)
  const { setLocation, showFilter, setShowFilter, showMutationNoetig } = store
  const height = isNaN(dimensions.height) ? 250 : dimensions.height
  const width = isNaN(dimensions.width) ? 250 : dimensions.width - 1
  const abteilungen = store.abteilungenFiltered

  return (
    <Container>
      <List
        height={height}
        itemCount={abteilungen.length}
        itemSize={50}
        width={width}
      >
        {({ index, style }) => {
          const row = abteilungen[index]
          const showDelSvg = row.deleted === 1
          const showMutationNoetigSvg =
            row.mutationNoetig === 1 && showMutationNoetig

          return (
            <Row
              style={style}
              onClick={() => {
                setLocation(['Abteilungen', row.id.toString()])
                if (showFilter) setShowFilter(false)
              }}
              active={!showFilter && activeId === row.id}
            >
              <RowContainer>
                <Text>{`${row.name || ''}`}</Text>
                {showMutationNoetigSvg && (
                  <Svg>
                    {row.mutationFrist && (
                      <>
                        <MutationFrist id={`mutationFrist${row.id}`}>
                          {row.mutationFrist}
                        </MutationFrist>
                        <UncontrolledTooltip
                          placement="bottom"
                          target={`mutationFrist${row.id}`}
                        >
                          Frist für Handlungs-Bedarf
                        </UncontrolledTooltip>
                      </>
                    )}
                    <FaRegEdit id={`mutationNoetig${row.id}`} />
                    <UncontrolledTooltip
                      placement="bottom"
                      target={`mutationNoetig${row.id}`}
                    >
                      Handlungs-Bedarf vorhanden
                    </UncontrolledTooltip>
                  </Svg>
                )}
                {!showDelSvg && !showMutationNoetigSvg && ''}
                {showDelSvg && (
                  <Svg>
                    <FaTrashAlt id={`deletedIcon${row.id}`} />
                    <UncontrolledTooltip
                      placement="bottom"
                      target={`deletedIcon${row.id}`}
                    >
                      Diese Abteilung wurde gelöscht
                    </UncontrolledTooltip>
                  </Svg>
                )}
              </RowContainer>
            </Row>
          )
        }}
      </List>
    </Container>
  )
}

export default observer(AbteilungList)
